import React, { useState } from 'react';
import { useParams } from 'react-router';

import Structure from '../Structure';
import Login from './Pages/Login';
import SignUp from './Pages/SignUp';
import ForgotPassword from './Pages/ForgotPassword';

const Authe = () => {
    const { pageName } = useParams();

    const [state, setState] = useState({
        pageName: pageName === 'login' ? 'login' : pageName === 'signup' ? 'signup' : 'forgotPassword',
    });

    const pageHandler = (key, value) => {
        if(value === 'login'){
            window.history.pushState('', 'Login', '/authe/login');
        }
        if(value === 'signup'){
            window.history.pushState('', 'Login', '/authe/signup');
        }
        if(value === 'forgotPassword'){
            window.history.pushState('', 'Login', '/authe/forgot-password');
        }
        state[key] = value;
        setState({ ...state });
    };

    return (
        <>
            <Structure>
                {state.pageName === 'login' && <Login pageHandler={pageHandler} />}
                {state.pageName === 'signup' && <SignUp pageHandler={pageHandler} />}
                {state.pageName === 'forgotPassword' && <ForgotPassword pageHandler={pageHandler} />}
            </Structure>
        </>
    );
};

export default Authe;
